import React from 'react'
import classnames from 'classnames'
import pic from '../images/mecirc.png'
import Labeler from './Labeler/Labeler'

const LeftPanel = () => {

	const getClass =()=>{
		let pg = document.getElementById('landing-main')
		if(!pg){return 'hidden'}
		pg = pg.getBoundingClientRect().bottom;
		return classnames('left-panel-main', {slideInLeft: pg<=0 }, {hidden: pg>0})

	}

	const getMessage = () =>{
		let node = document.getElementById('contact');
		if(node && node.getBoundingClientRect().top<window.innerHeight-300){
			return '';
		}
		node = document.getElementById('portfolio');
		if(node && node.getBoundingClientRect().top<window.innerHeight/2){
			return 'Portfolio';
		}
		node = document.getElementById('about');
		if(node && node.getBoundingClientRect().top<window.innerHeight/2){
			return 'About';
		}
		node = document.getElementById('featured-work');
		if(node && node.getBoundingClientRect().top<window.innerHeight/2){
			return 'Featured Work';
		} 
		return ''
	}
	
	return(
		<div className='left-panel-main' id='left-panel'>
			<Labeler message={getMessage()} on={true}/>
		</div>
)}

export default LeftPanel;


