import React from 'react'
import Slider from 'react-slick'
import pic1 from '../../images/mapper/pic1.jpeg'
import pic2 from '../../images/mapper/pic2c.jpeg'
import pic3 from '../../images/mapper/pic3.jpeg'
import pic4 from '../../images/mapper/pic4.jpeg'
import pic5 from '../../images/mapper/pic5.jpeg'
import dataMapper from '../../images/dataMapper.png'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";


class FeaturedWork extends React.Component {
	render(){
		const imageAr = [pic1,pic2,pic3,pic4,pic5];
		 const settings = {
	     	customPaging: function(i) {
	     	  return (
	     	    
	     	      <img src={imageAr[i]} className="featured-image-thumb" />
	     	    
	     	  );
	     	},
	     	dots: true,
	     	dotsClass: "slick-dots slick-thumb",
	     	infinite: true,
	     	vertical: true,
	     	arrows:false,
	     	speed: 500,
	     	autoplay:true,
	     	autoplaySpeed: 2000,
	     	slidesToShow: 2,
	     	slidesToScroll: 1
	    };
		return(
			<div className="featured-work-container" >
				<div className="featured-inner-container" id="featured-work">
					<div className="slider-container">
						<Slider {...settings}>
				        	{imageAr.map(image=>(
								<div className="featured-image-container">
				        			<img className="featured-image" src={image} />
				        		</div>
				        		))}      	
				        </Slider>
				    </div>
				    <div className="featured-right">
				    	<img style={{width:"15vw", minWidth:'200px'}} src={dataMapper} />
				    	<p>Data Mapper is a full stack web application that I worked on in early 2018. It uses data from the U.S. Census Bureau, broken up by congressional district and creates a colored visualization of that data overlayed onto a map of the United States.</p>
						<p>Data Mapper offers customization options to control what data is being mapped and how the visualizer displays the data. Using Data Mapper, it is possible to visualize an uncountable number data of sets by congressional district.</p>
						<p>Interested in the distribution of wealth or ethnicity around the country? This is one example of what Data Mapper allows you to visualize with ease.</p>
						<h4>Technologies Used</h4>
							<ul className="featured-ul">
								<div><div><li>Javascript</li></div></div>
								<div><div><li>NodeJS</li></div></div>
								<div><div><li>React-Redux</li></div></div>
								<div><div><li>react-leaflet</li></div></div>
								<div><div><li>Express</li></div></div>
								<div><div><li>MongoDB</li></div></div>
							</ul>
							<div className="link1"> <a href="https://msimms1123.github.io/dataMapper/">View Demo</a> </div>				    	
				    </div>			
				</div>
			</div>
		)
	}
}

export default FeaturedWork;


