import React from 'react'

class Labeler extends React.Component {
	constructor(){
		super();
		this.state={			
			msgTime: [],
			on: false,
			flipTime:500
		}
	}
	getPoints(center,rad,i){
		let theta=Math.PI/6;
		let pointString='';
		let mult;
		if(this.state.msgTime[i]< this.state.flipTime){
			mult = Math.sin((Math.PI*3/2)*(this.state.msgTime[i]/this.state.flipTime ))
		}else{
			mult = 1;
		}
		if(mult==0){return '0,0'}
		for(let i=0; i<6; i++){
			let angle = theta + i*Math.PI/3;
			let point = [rad*Math.cos(angle)*mult, rad*Math.sin(angle)];
			pointString += " "+(point[0]+center[0])+","+(point[1]+center[1]);
		}
		return pointString;

	}
	getRot(i){
		if(this.state.msgTime[i]< this.state.flipTime){
			return -270+(270)*(this.state.msgTime[i]/this.state.flipTime)
		}else{
			return 0
		}
	}
	turnPoly(i){
		
		setTimeout(()=>{this.turn(i)}, 100)
		if(i<this.state.msgTime.length-1){
			setTimeout(()=>{this.turnPoly(i+1, this.state.msgTime)},this.state.flipTime/(2*this.state.msgTime.length))
		}
	}
	turn(i){
		if(this.state.msgTime[i]< this.state.flipTime){
			let newMsgTime = this.state.msgTime;
			newMsgTime[i]+=100
			this.setState({msgTime: newMsgTime})
			setTimeout(()=>{this.turn(i)},100)
		}
	}
	render(){
		let message = Array.from(this.props.message);
		if(this.state.msgTime.length !== message.length){
			this.state.msgTime= new Array(this.props.message.length);
			for(let i = 0; i<this.state.msgTime.length; i++){
				this.state.msgTime[i]=0;
			}			
			this.turnPoly(0, this.state.msgTime);
			
		}
		let rad = (window.innerHeight - 250 )/((1+Math.sqrt(3)/2)*message.length) ;
		return (
			<span>
				<svg className="labeler-main labeler-display" style={{width:rad*2}}>
					{message.map((c,i)=>(					
						<polygon points={this.getPoints([rad,2*rad*(i)+rad],rad,i)} stroke='#fff' strokeWidth={2} fill='#079'/>						
						))}
				</svg>
				<div className="labeler-main label-text">
					{message.map((c,i)=>(					
						<div className="label-ch" style={{height:rad*2, fontSize: rad*1.4, transform:'rotateY('+this.getRot(i)+'deg)'}}>
							{c}
						</div>
						))}
				</div>
			</span>
		)
	}
}

export default Labeler;


