import React from 'react'
import image from '../../images/about.png'
import piechart from '../../images/piechart.png'
import fast from '../../images/fast.png'
import clean from '../../images/clean.png'
import responsive from '../../images/responsive.png'
import personalized from '../../images/personalized.png'
import fast2 from '../../images/fast2.png'
import clean2 from '../../images/clean2.png'
import responsive2 from '../../images/responsive2.png'
import personalized2 from '../../images/personalized2.png'

const About = ({}) => {
	return (
		<div className="about-main" id="about">

				
			<div className="clearfix about-top-container">
				<div className="about-image-container">
					<img className="about-image" src={image}/>
				</div>
				<div className="piechart-container">
					<img className="piechart" src={piechart}/>
				</div>
				<div className="about-text clearfix">
					<h2 className="thisGuy">Who's This Guy?</h2>
					<p>Hi, my name is Michael Simms. I have recently started in the field of web development and I have discovered that I love it!</p>
					<p>I have a B.S. in Mathematics from Carnegie Mellon Unviersity. Mathematics was always my passion, but I hated that I could never see my equations manifest in reality. Web development is my solution to that.</p>
					<p>I haven't been working in this field for very long, but I have honed my skills with many personal projects and a few paid works for clients.</p>
					<p>Although I make no secret of the fact that I am new to the game, my skills have developed quickly because I am an ambitious and zealous learner and worker. My mathematical education has also been invaluable. The use of trigonometry, linear algebra, calculus, etc. enables me to make my website more dynamic. </p>
					<p>Please check out some of my work below and see what I can do!</p>
				</div>
			</div>
			<hr/>
			<div className="about-footer">
				<div className="strengths">
					<img className="strengths-inner-image" src={fast2}/>
					<img className='strengths-inner-text' src={fast}/>
				</div>
				<div className="strengths">
					<img className="strengths-inner-image" src={clean2}/>
					<img className='strengths-inner-text' src={clean}/>
				</div>
				<div className="strengths">
					<img className="strengths-inner-image" src={personalized2}/>
					<img className='strengths-inner-text' src={personalized}/>
				</div>
				<div className="strengths">
					<img className="strengths-inner-image" src={responsive2}/>
					<img className='strengths-inner-text' src={responsive}/>
				</div>
			</div>
		</div>
	)
}

export default About;


