import React from 'react'
import classnames from 'classnames'

const RightBorder = ({}) => {
	const getClass =()=>{
		let pg = document.getElementById('landing-main')
		if(!pg){return 'hidden'}
		pg = pg.getBoundingClientRect().bottom;
		return classnames('right-border-panel', {slideInRight: pg<=0 }, {hidden: pg>0})

	}
	return(
		<div className='right-border-panel'>
		</div>
	)
}

export default RightBorder;


