import React from 'react'
import ContentBlock from './ContentBlock'
import classnames from 'classnames'

const Container = ({data, page, back, forID}) =>{
	let col = back? '#09b': '#ddd'
	let backClass = classnames({backcontainer: back }, {notback: !back})
	return(
	<div className={backClass} id={'back-container-'+forID} style={{
		background: col, 
	}}>
		<div className="content-block-container" id={'container-'+forID} >
			<ContentBlock data={data} page={page} back={back} forID={forID}/>
		</div>
	</div>
	)}

export default Container


