import React from 'react'

class Contact extends React.Component{
	constructor(){
		super();
		this.state = {
			timer:0,
			turning:false,
			rot:0
		};
	}
	turn(){
		//console.log(this.state.turning);
		if(this.state.rot <360 && this.state.turning){
			this.setState({rot:this.state.rot+20});
			setTimeout(()=>this.turn(),35);
		}
	}
	unturn(){
		if(this.state.rot >0 && !this.state.turning){
			this.setState({rot:this.state.rot-20});
			setTimeout(()=>this.unturn(),35);
		}
	}
	componentDidMount(){
		setInterval(()=>{this.setState({timer: this.state.timer + 50})},50)
	}

	getPoints(center,rad){
		let theta=Math.PI/6;
		let pointString='';
		let points2 = [300,30,0,30,-300,30,-300,-30,0,-30,300,-30];	
		let m = this.state.rot/360;
		let n = 1-m;		
		for(let i=0; i<6; i++){
			let angle = theta + i*Math.PI/3;
			let point = [rad*Math.cos(angle), rad*Math.sin(angle)];
			pointString += " "+((n*point[0]+m*points2[2*i])+center[0])+","+((n*point[1]+m*points2[2*i+1])+center[1]);
		}
		return pointString;

	}

	getBorderColor(){
		let mult = Math.sin(this.state.timer/500);
		mult  = (mult+1)/2
		return 'rgb('+ (255 - mult*255) +','+(255)+','+(255)+')'
	}

	getBorderWidth(){
		let mult = Math.sin(this.state.timer/500);
		mult  = (mult+1)/2
		return 3*mult + 1;
	}

	getContent(){
		if(this.state.rot<90){
			return (<span>Let's<br/>Build<br/>Something!</span>)
		}else if (this.state.rot>270 && window.innerWidth<600 ){
			return (<span className='email-me'>Email Me: <span style={{fontSize:"18px", color:'white'}}>mike@mikeprograms.com</span> </span>)
		}else if(this.state.rot>270){
			console.log(window.innerWidth)
			return (<span className='email-me'>Email Me: <span style={{fontSize:"24px", color:'white'}}>mike@mikeprograms.com</span> </span>)
		}else{
			return <span></span>
		}
	}
	render(){
		let rad = 150;
		return (
			<div className="contact-main" id='contact'>
				<div >
					<svg className="contact-top">
						<line x1="0" y1="60" x2={window.innerWidth/2} y2="0" stroke="white" strokeWidth={2}/>
						<line x1={window.innerWidth} y1="60" x2={window.innerWidth/2} y2="0" stroke="white" strokeWidth={2}/>
						<polygon points={"0,60 "+(window.innerWidth/2 +",2 ")+(window.innerWidth+",60")} fill='#079'/>
					</svg>
				</div>
				<div className="contact-inner"> 
					<svg className="contact-svg" style={{transform:'rotateY('+this.state.rot+'deg)'}}>
						<polygon points={this.getPoints([window.innerWidth/2,150],rad)} stroke={this.getBorderColor()} strokeWidth={this.getBorderWidth()} fill='#555' />
					</svg>
					<div className="contact-text" style={{transform:'rotateY('+this.state.rot+'deg)'}}
					onMouseEnter={()=>{
						if(!this.state.turning){
							this.state.turning=true;
							this.turn();
						}					
					}}
					onMouseLeave={()=>{
						if(this.state.turning && this.state.rot ==360){
							this.state.turning = false;
							this.unturn();
						}
					}}
					onClick={()=>{document.location = "mailto:mike@mikeprograms.com"}}
					> 
						{this.getContent()}
					</div>
					
					<div>Icons made by <a href="https://www.flaticon.com/authors/good-ware" title="Good Ware">Good Ware</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0" target="_blank">CC 3.0 BY</a></div>
				</div>
			</div>
		)
	}
}

export default Contact


