import React from 'react'
import logo from '../images/logo.png'

const Navbar = ({})=>(
	<div className="nav-container">
		<img className="logo" src={logo}/>
		<div className="nav-sub-container">
			<div className="nav-li clickable"
			onClick={()=>document.getElementById("featured-work").scrollIntoView({behavior:'smooth', block:'center'})}
			>Featured Work</div>
			<div className="nav-li clickable"
			onClick={()=>document.getElementById("about").scrollIntoView({behavior:'smooth', block:'center'})}
			>About</div>
			<div className="nav-li nav-pers-container">
				Personal Projects
				<div className="sub-menu">
					<div className="nav-pers clickable "
						onClick={()=>document.getElementById("container-0").scrollIntoView({behavior:'smooth', block:'center'})}
					>Epidemic</div>
					<div className="nav-pers clickable "
					onClick={()=>document.getElementById("container-1").scrollIntoView({behavior:'smooth', block:'center'})}
					>Data Mapper</div>
					<div className="nav-pers clickable "
					onClick={()=>document.getElementById("container-2").scrollIntoView({behavior:'smooth', block:'center'})}
					>Checkers AI</div>
				</div>
			</div>
			<div className="nav-li nav-prof-container">
				Professional Projects
				<div className="sub-menu">
					<div className="nav-prof clickable "
						onClick={()=>document.getElementById("container-3").scrollIntoView({behavior:'smooth', block:'center'})}
					>Wind Horse Circle</div>
					<div className="nav-prof clickable "
					onClick={()=>document.getElementById("container-4").scrollIntoView({behavior:'smooth', block:'center'})}
					>Steve's Plave</div>
				</div>
			</div>
			<div className="nav-li clickable"
			onClick={()=>document.getElementById("contact").scrollIntoView({behavior:'smooth', block:'center'})}
			>Contact Me</div>
		</div>
	</div>)


export default Navbar


