import React from 'react'
import steve from '../../images/steves.png'
import wind from '../../images/windhorse.png'
import mapper from '../../images/mapperCollage.png'
import checkersgif from '../../images/checkersgif.gif'
import epidemic from '../../images/epidemic.png'
import mikeConnect from '../../images/mikeConnect.png'
import classnames from 'classnames'



class ContentBlock extends React.Component{
	constructor(){
		super();
		this.state = {time:0}
	}
	runTimer(){
		console.log(this.state.time +'testing123');
		this.setState({time: this.state.time + 100});
		let timer = setInterval(()=>{this.setState({time: this.state.time + 100})},100)
		setTimeout(()=>{
			clearInterval(timer); 
			console.log('cleared'); 
			this.setState({time:30000})},6000);
	}
	getClass(id, addend, defaultClass, delay){ 
		let node = document.getElementById(id);

		if(!node ){
			return classnames(defaultClass, 'clearfix', 'show');
		}
		if(delay&&delay>this.state.time){
			return classnames(defaultClass, 'hidden', 'clearfix');
		}

		let show = 'show'+ addend;
		let hide = 'hide';
		let top = node.getBoundingClientRect().top;
		
		if(top<window.innerHeight*.75 && defaultClass==='content-title' && this.state.time ==0){
			this.runTimer();
		}
		if(top>window.innerHeight && this.state.time>0 && defaultClass==='content-title'){
			this.setState({time:0})
		}

		
		return classnames(defaultClass, 'clearfix', {[show]:  top<window.innerHeight*.75},{[hide]: top>window.innerHeight*.75} );

	}
	componentDidMount(){

	}
	render(){
		let images = [ epidemic, mapper, checkersgif,steve,wind]
		let image = images[this.props.forID%5];
		let data = this.props.data;
		let class2 = this.props.back? 'content-left': 'content-right' 
		let class1 = this.props.back? 'content-right': 'content-left' 
		let contID = 'container-' + this.props.forID
		let descriptionSplit = data.description.split('\n');
		return (
			<div  className='content-container'  id={data.title + '1'}>
				<div className={this.getClass(contID, '', 'content-title')} id={data.title} style={{color:data.evenOdd? 'white':'#079'}}>{data.title}</div>
				<div className={"clearfix "+class1}>
					<div className={this.getClass(contID, '1', 'pic-div')} style={{backgroundImage: 'url(../../images/profback.png)'}}>
						<div className="pic-inner">
							<img className="pic" src={image}/>
							
							<ul className='list-stuff' style={{color:data.evenOdd? 'white':'#079'}}>
								{data.list.map((x,i)=>(
									<li 
										className={this.getClass(contID, '2', 'fact-li', 200*i)}
										style={{left:100*i}}
										>{x}</li> 
									))}
							</ul>
							</div>						
					</div>
				</div>
				<br/>
				<div className={"clearfix "+class2}>
					<div className={this.getClass(contID, '3', "content-description")}>
						{descriptionSplit.map((block)=>(
							<span>
								{block}
								<br/>
								<br/>
							</span>
						))}	
					<a href={data.url}>
						<div className={"link"+(this.props.forID%2 + 1) + " link-abs"}> {Math.floor(this.props.forID/2)?"Visit Site":"View Demo"}</div>
					</a>
					</div>
				</div>
			</div>
		)
	}
}

export default ContentBlock;


